* {
  font-family: sans-serif, Helvetica, sans-serif;
  padding: 0;
  margin: 0;
  font-size: 12pt;
  color: #242424;
}

.app {
  max-width: 800px;
  margin: 0px auto;
  margin-top: 48px;
  min-height: 600px;
  border: 1px solid #ccc;
  padding: 24px;
}

.content {
}

.content-text {
}

.content-image {
  padding-top: 48px;
  max-width:700px;
}

.options {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  margin-top: 48px;
  margin-bottom: 32px;
  margin-left: -9px;
  /* align-items: flex-end; */
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 6px 9px;
  font-size: 14px;
  border-bottom: 1px solid #c7c7c7;
  width: fit-content;
  margin-top: 9px;
  cursor: pointer;
}

.button-number {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #bc004d;
  color: white;
  padding: 3px;
  border-radius: 16px;
  width: 19px;
  margin-right: 6px;
}

.button:hover {
  background: #ccf5ff;
}
